@import '~styles/colors';

.Avatar {
  max-width: 120px;
  max-height: 120px;
}

.Avatar__image-container {
  position: relative;
  width: 100%;
  height: 100%;

  > img {
    border: 1px solid rgba($gray,0.5);
    border-radius: 50%;
    bottom: 0;
    height: auto;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
  }
}
